	import listCell from '@/components/mix-list-cell';
	import auth from '@/components/auth.vue';
	import api from '@/common/vmeitime-http/'
    import {  
        mapState , mapMutations
    } from 'vuex';  
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			listCell,
			auth
		},
		data(){
			return {
				user: {},
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				hasphone: true,
				moving: false,
				footerlistdata: []
			}
		},
		onLoad(){

			// console.log(uni.getStorageSync('haslogin'))
			if (uni.getStorageSync('haslogin') !== '') {
				this.login(uni.getStorageSync('userInfo'))
			}
			if (uni.getStorageSync('hasphone') == '') {
				this.hasphone = false
			}

			









			this.userlist();
			this.footerlist()
		},
		onPullDownRefresh(){
			// console.log(this.haslogin)
			if(uni.getStorageSync('haslogin') == '')  {
				this.$refs.open.openauth()
				uni.stopPullDownRefresh()
			} else {
				this.userlist(true);
				this.footerlist();
			}
		},
		onShareAppMessage(res) {
		  return {
			title: '贝贝商城',
			imageUrl: '/static/temp/banner3.jpg',
			path: '/pages/index/index'
		  }
		},
        computed: {
			...mapState(['hasLogin','userInfo'])
		},
        methods: {
			...mapMutations(['login']),
			getPhoneNumber: function(e) {
				let that = this;
				if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {
					console.log('已拒绝获取手机号！')
				} else {
					uni.showLoading({title: '正在加载'})
					var options = {
						encryptedData: e.detail.encryptedData,
						iv: e.detail.iv
					}
					that.bindphone(options)
				}
			},
			bindphone(options) {
				let that = this;
				let loginurl = '/auth/bindPhone';
				api.httppost(loginurl, options).then((res) => {
					if (res.data.errno == 0) {
						uni.setStorageSync('hasphone', true);
					}
					uni.showToast({title: res.data.errmsg, icon: 'none'})
					uni.hideLoading()
				}).catch((err) => {
					uni.hideLoading()
				})
			},
			auth() {
				if(uni.getStorageSync('haslogin') == '') this.$refs.open.openauth()
			},
			getdata() {
				this.userlist(true);
				this.footerlist();
			},
			userlist(flag) {
				let historyurl = '/user/index'
				uni.showLoading({title: '正在加载'})
				api.httpget(historyurl).then((res) => {
					let respons = res.data
					if (respons.errno == 0) {
						this.user = respons.data.order
					}
					else {
						uni.showToast({title: respons.errmsg, icon: 'none'})
					}
					uni.hideLoading()
					if(flag) uni.stopPullDownRefresh();
				})
			},
			footerlist(flag) {
				let historyurl = '/footprint/list?page=1&limit=10'
				api.httpget(historyurl).then((res) => {
					let respons = res.data
					if (respons.errno == 0) {
						this.footerlistdata = respons.data.list
					} else {
						uni.showToast({title: respons.errmsg, icon: 'none'})
					}
					uni.hideLoading()
					if(flag) uni.stopPullDownRefresh();
				})
			},
			/**
			 * 统一跳转接口,拦截未登录路由
			 * navigator标签现在默认没有转场动画，所以用view
			 */
			navTo(url){
				if(!this.hasLogin){
					url = '/pages/public/login';
				}
				uni.navigateTo({url})  
			}, 
			navTopro(id){
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
			/**
			 *  会员卡下拉和回弹
			 *  1.关闭bounce避免ios端下拉冲突
			 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
			 *    transition设置0.1秒延迟，让css来过渡这段空窗期
			 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
			 */
			coverTouchstart(e){
				if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			coverTouchmove(e){
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}
		
				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			}
        }  
    }  
